import React, { useEffect, useState } from "react";

const MediaRenderer = ({ url, onClick, className }) => {
  const [isImage, setIsImage] = useState(null); // null = loading, true = image, false = video
  const [isIphone, setIsIphone] = useState(false);
  const [base64Image, setBase64Image] = useState(null);

  // Detect if device is an iPhone
  useEffect(() => {
    const userAgent = navigator.userAgent || "";
    if (/iPhone|iPad|iPod/i.test(userAgent)) {
      setIsIphone(true);
    }
  }, []);

  // Handle image or video loading and optional AVIF to base64 for iPhone
  useEffect(() => {
    if (!url) return;

    const isAvif = url.endsWith(".avif");

    if (isIphone && isAvif) {
      // Convert AVIF to base64 for iPhone
      fetch(url)
        .then((res) => res.blob())
        .then((blob) => {
          const reader = new FileReader();
          reader.onloadend = () => {
            setBase64Image(reader.result); // base64 string
            setIsImage(true);
          };
          reader.onerror = () => setIsImage(false);
          reader.readAsDataURL(blob);
        })
        .catch(() => setIsImage(false));
    } else {
      const img = new Image();
      img.onload = () => setIsImage(true);
      img.onerror = () => setIsImage(false);
      img.src = url;
    }
  }, [url, isIphone]);

  if (isImage === null) return <p></p>;

  return (
    <>
      {isImage ? (
        isIphone && url?.endsWith(".avif") ? (
          <img
            src={base64Image || ""}
            alt="media"
            className={className}
            style={{ maxWidth: "100%" }}
            onClick={onClick}
          />
        ) : (
          <picture>
            {url?.endsWith(".avif") && (
              <source srcSet={url} type="image/avif" />
            )}
            <img
              src={
                url?.endsWith(".avif")
                  ? url.replace(".avif", ".jpg")
                  : url || ""
              }
              alt="media"
              className={className}
              style={{ maxWidth: "100%" }}
              onClick={onClick}
            />
          </picture>
        )
      ) : (
        <video controls style={{ maxWidth: "100%" }} onClick={onClick}>
          <source src={url || ""} />
        </video>
      )}
    </>
  );
};

export default MediaRenderer;
